@import url("https://fonts.googleapis.com/css2?family=Raleway&display=swap");

html,
body,
#root {
  width: 100vw;
  height: 100vh;
  padding: 0;
  margin: 0;
  font-family: "Raleway", sans-serif;
}

.app {
  width: 100%;
  height: 100%;
  text-align: center;
  user-select: none;
}

.navbar {
  padding: 10px;
  background-color: #e5e5e5;
  border-bottom: 1px solid #b8b5ba;
  a {
    color: black;
    text-decoration: none;
    font-size: 24px;
  }
}

.gallery-preview {
  display: inline-block;
  margin: 20px;
  padding: min(20px, 1vw);
  padding-top: 10px;
  border: 1px solid #ccc;
  border-radius: 10px;
  cursor: pointer;
  max-width: 90%;

  .s {
    width: min(80vw, 1000px);
  }
}

.gallery-preview:hover {
  border: 1px solid #000;
}

.album-preview {
  display: inline-block;
  margin: 20px;
  padding: min(20px, 1vw);
  padding-top: 10px;
  border: 1px solid #ccc;
  border-radius: 10px;
  cursor: pointer;
  max-width: 80%;
}

.album-preview:hover {
  border: 1px solid #000;
}

.album-page {
  h1 {
    padding: 20px;
    margin: 0;
  }
  min-height: 100vh;
  display: flex;
  justify-content: center;
}

.nav-cover {
  position: absolute;
  left: 0;
  top: 0;
  background-color: white;
  width: 100%;
  height: 100%;
}

.back-button {
  position: absolute;
  top: 1.5px;
  left: 10px;
  padding: 10px;
  font-size: 18px;
  font-family: "Raleway", sans-serif;
  cursor: pointer;
}

.image {
  display: inline-block;
  margin: min(20px, 1vw);
  border: 2px solid #ccc;
  border-radius: 10px;
  cursor: pointer;
}

.x {
  width: min(75vw, 400px);
}

.s {
  width: min(75vw, 500px);
}

.m {
  border-radius: 0;
  padding: 0;
  max-width: 95%;
  // width: min(95%, 1000px);
  max-height: 95%;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  cursor: default;
}
